import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { IHttpPostParams } from '../http/http.interfaces';
import { ICommand } from './command.interface';

export class Command implements ICommand {
  constructor(private resourceType: string, private httpClient: HttpClient) {}

  execute(data?: object, options?: IHttpPostParams): Observable<any> {
    const payload =
      data instanceof FormData
        ? data
        : {
            ...data,
            resourceType: this.resourceType
          };

    return this.httpClient.post('/api/rx/application/command', payload, options as any);
  }
}
